import { NavLink } from "react-router";

export default function Header() {
  return (
    <div className="navbar bg-white shadow-lg min-w-full border-b-2 border-gray-300">
      <div className="navbar-start"></div>
      <div className="navbar-center lg:flex">
        <NavLink to="/" className="btn shadow-none border-none bg-white text-xl">
          <img
            src="https://i.ibb.co/xqC8D34h/Dinero.png"
            alt="logoBrand"
            width="75"
            height="75"
          />
        </NavLink >
      </div>
      <div className="navbar-end">
        <div className="dropdown dropdown-end text-xl">
          <button className="btn shadow-none btn-circle border-none bg-white text-xl hover:text-green-500 text-gray-500">
            <i class="fa-solid fa-user"></i>
          </button>
          <ul
            tabIndex={0}
            className="menu menu-sm dropdown-content text-black bg-white shadow-sm border-b-2 border-gray-300 rounded-box z-1 mt-3 w-52 p-2"
          >
            <li className="border-b-2 border-gray-300">
              <NavLink to="/profile" className="justify-between">
                Profile
              </NavLink>
            </li>
            <li className="border-b-2 border-gray-300">
              <a>Login</a>
            </li>
            <li>
              <a>Logout</a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
}
