import axios from "axios";
import { useEffect, useState } from "react";

export default function Pofile(props) {

  return (
    <div className="m-7 text-black">
      <div className="grid grid-cols-1 gap-4">
        <div>
          <h2 className="text-2xl ml-5">Profile</h2>
          <ul className="space-y-7 p-5">
            <li>
              <div className="card card-border border-none rounded bg-white shadow-lg overflow-hidden">
                <div className="card-body">
                  <ul>
                    <li className="p-2">
                      <div className="md:justify-self-center">
                        <img
                          src={props.user.img}
                          alt={props.user.name}
                          width="100"
                          height="100"
                        />
                      </div>
                    </li>
                    <li className="p-2">
                      <div className="md:justify-self-start">
                        <h3 className="font-bold">Name</h3>
                        <p className="text-gray-500">{props.user.name}</p>
                      </div>
                    </li>
                    <li className="p-2">
                      <div className="md:justify-self-start">
                        <h3 className="font-bold">Mail</h3>
                        <p className="text-gray-500">{props.user.email}</p>
                      </div>
                    </li>
                    <li className="p-2">
                      <div className="md:justify-self-start">
                        <h3 className="font-bold">Type</h3>
                        <p className="text-gray-500">{props.user.type}</p>
                      </div>
                    </li>
                    <li className="p-2">
                      <div className="md:justify-self-start">
                        <h3 className="font-bold">Address</h3>
                        <p className="text-gray-500">{props.user.address}</p>
                      </div>
                    </li>
                    <li className="p-2">
                      <div className="md:justify-self-start">
                        <h3 className="font-bold">Biography</h3>
                        <p className="text-gray-500">{props.user.bio}</p>
                      </div>
                    </li>
                  </ul>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
}
