import axios from "axios";
import { useState } from "react";

export default function Hero(props) {
  let [title, setTitle] = useState("");
  let [body, setBody] = useState("");

  const postOffer = (e) => {
    axios
      .post("/newOffer", {
        title: title,
        body: body,
        date:Date(Date.now()).toString()
      })
      .then((res) => {
        alert(res.data.message);
      })
      .catch((err) => {
        console.log("Error: " + err);
      });

      e.preventDefault();
  };
  return (
    <div className="grid grid-cols-1 md:grid-cols-2 justify-items-start text-black bg-white shadow-lg p-7">
      <div>
        <h1 className="text-2xl">
          Hi , {props.user.name}
          <i class="fa-solid fa-heart ml-2 text-red-500"></i>
        </h1>
        <span className="text-gray-500">
          Skill meets opportunity  for every age, everyschedule.
        </span>
      </div>
      {props.user.type == "company" ? (
        <div className="md:justify-self-end md:mt-0 mt-2">
          <label
            htmlFor="Job"
            className="btn bg-yellow-500 border-0 text-white rounded"
          >
            New job<i class="fa-solid fa-plus ml-1"></i>
          </label>
        </div>
      ) : (
        ""
      )}
      <input type="checkbox" id="Job" className="modal-toggle" />
      <div className="modal z-50" role="dialog">
        <div className="modal-box bg-white rounded w-xs md:w-lg overflow-hidden">
          <h3 className="text-lg font-bold mb-3">New job</h3>
          <form onSubmit={postOffer}>
            <ul className="space-y-3">
              <li>
                <span className="font-bold">Title</span>
                <br />
                <input
                  type="text"
                  onChange={(e)=>setTitle(e.target.value)}
                  className="input text-md border-2 rounded bg-white border-black text-gray-500 p-2 w-full"
                />
              </li>
              <li>
                <span className="font-bold">Details</span>
                <br />
                <textarea
                  type="text"
                  onChange={(e)=>setBody(e.target.value)}
                  className="input text-md border-2 rounded bg-white border-black h-20 text-gray-500 p-2 w-full"
                ></textarea>
              </li>
              <li>
                <input type="submit" value="Add" className="btn bg-green-500 border-0 text-white rounded"/>
              </li>
            </ul>
          </form>
        </div>
        <label className="modal-backdrop" htmlFor="Job">
          Close
        </label>
      </div>
    </div>
  );
}
