export default function Faq() {
  const faqs = [
    {
      question: "What is dinero?",
      answer:
        "dinero is a platform designed to help undergraduate students find entry-level job opportunities and internships. We also provide training courses to boost your skills if you dont get hired on your first try.",
    },
    {
      question: "Who can use this platform?",
      answer:
        "Our platform is primarily designed for current undergraduate students and recent graduates who are looking to kickstart their careers.",
    },
    {
      question: "How does the job hiring process work?",
      answer:
        "You can browse and apply for job listings directly on our platform. Employers review applications and may reach out for interviews. We also provide tips and resources to improve your application success.",
    },
    {
      question: "What happens if I dont get hired?",
      answer:
        "No worries! If you dont secure a job through your initial applications, we offer affordable, high-quality courses that help improve your resume, skills, and job-readiness. You can apply again after completing these courses.",
    },
    {
      question: "What kind of courses do you offer?",
      answer:
        "We offer short, practical courses in areas like resume building, interview skills, communication, basic tech skills, and morefocused on what employers are really looking for.",
    },
    {
      question: "Are the courses free?",
      answer:
        "Some of our basic resources are free, and we also offer premium courses at student-friendly prices. We occasionally provide scholarships and discounts.",
    },
    {
      question: "How do I know which course is right for me?",
      answer:
        "Once you register, you can take a short assessment or speak with one of our career mentors to get personalized course recommendations.",
    },
    {
      question: "Can I get a job without taking a course?",
      answer:
        "Yes! You can apply to jobs right away. The courses are there to support you in case you need help improving your chances.",
    },
    {
      question: "Do I need a CV to apply for jobs?",
      answer:
        "Yes, a well-prepared CV helps a lot. If you dont have one, we offer tools and guidance to help you create a professional CV quickly.",
    },
    {
      question: "How do employers use this platform?",
      answer:
        "Employers post job listings, internships, and projects targeted at undergraduate talent. They can view your profile, applications, and reach out directly.",
    },
    {
      question: "Is there any support if I get stuck?",
      answer:
        "Absolutely. Our support team and mentors are here to help you with applications, course choices, and career advice.",
    },
    {
      question: "How do I get started?",
      answer:
        "Just sign up for a free account, complete your profile, and start exploring jobs or courses that match your interests and skills.",
    },
  ];

  return (
    <div className="m-7 text-black">
      <div className="grid grid-cols-1 gap-4">
        <div>
          <h2 className="text-2xl ml-5">The FAQ</h2>
          <ul className="space-y-7 p-5">
            <li>
              <div className="card card-border border-none rounded bg-white shadow-lg">
                <div className="card-body">
                  <ul>
                    {faqs.map((f) => {
                      return (
                        <li>
                          <div className="collapse bg-white border-b-2 border-gray-300 rounded-none">
                            <input type="checkbox" />
                            <div className="collapse-title font-semibold">
                              {f.question}
                            </div>
                            <div className="collapse-content text-sm">
                                {
                                    f.answer
                                }
                            </div>
                          </div>
                        </li>
                      );
                    })}
                  </ul>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
}
